/**
* @desc         lists.
* @name         layouts
* @author       Anthony Munene
* @tested       N/A
* @requires     N/A
*/

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style-position: inside;
}

ul, nav ul {
  list-style-type: none;
  li {
    @include core-19();
  }
}

.list--numbered {
  list-style-type: none;
  padding-left: 0;
  margin-bottom: 25px;
  .numbered {
    margin: 0 0 20px 25px;
    padding-left: 15px;
    &:before {
      counter-increment: section; /* Increment the section counter */
      content: counter(section) ". "; /* Display the counter */
      position: absolute;
      left: -25px;
      top: 0;
      padding-right: 10px;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
}

.bullets {
  list-style-type: disc;
  list-style-position: inside;
  margin-left: 1em;
}

.lower-letters {
  list-style-type: lower-alpha;
}

.list--ticks {
  padding-left: 1.5em;

  li::before {
    color: $primary-green;
    content: "\2713"; // code for ''
    display: inline-block;
    margin-left: -1.5em;
    width: 1.5em;
  }
}

.list--inline {
  li {
    display: inline;
  }
}

.activity-list {
  padding-left: 0;
  li {
    display: inline-block;
  }
}

.activity-list__date {
  float: left;
  text-align: left;
  min-width: 170px;
  margin-right: 10px;
  > * {
    font-weight: bold;
  }
}

.activity-list__date--highlight {
  @extend .activity-list__date;
  font-weight: bold;
}

.activity-list__description {
  float: left;
  width: 400px;
}

.coms-alerts {
  img {
    margin-right: 10px;
  }
}

.dl--label-value {
  margin-bottom: 1em;
  overflow: hidden;
  dt, dd {
    float: left;
    margin-left: 0;
    margin-bottom: 0.25em;
    width: 50%;
  }
  dd {
    font-family: "nta", Arial, sans-serif;
    font-size: 19px;
    font-weight: bold;
    line-height: 1.31579;
    text-align: right;
  }
}

.action-list {
  list-style: none;
  margin: 0;
  padding: 0;
  clear: both;
  li {
    border-top: 1px solid $border-colour;
    padding: 0.5em 0;
    margin: 0;
    overflow: hidden;
    &:first-child {
      border-top: 1px solid $border-colour;
    }
  }
}

.action-list__grouped {
  border-top: none;
  p {
    margin: 0.4em 0;
    padding: 0;
  }
}

.list-noborder {
  li {
    border-top: 0;
    &:first-child {
      border-top: 0;
    }
  }
}

.two-col-list {

  .two-col-list__col {
    width: auto;
    float: none;
  }

  ul {
    margin: 0;
    li {
      margin: 0.7em 0;
      @include core-16;
    }
    &.two-col-list--padded {
      margin-bottom: 30px;
    }
  }

  @include media(tablet) {
    display: inline-block;

    .two-col-list__col {
      width: 48%;
    }
    .two-col-list__col--left {
      float: left;
    }
    .two-col-list__col--right {
      float: right;
    }

  }

}

.three-col-list {
  margin: 20px 0 0 0;

  .three-col-list__col {
    width: auto;
    float: none;
  }

  ul {
    margin: 0;
    li {
      margin: 0.7em 0;
      @include core-16;
    }
  }

  @include media(tablet) {
    display: inline-block;

    .three-col-list__col {
      width: 29%;
      margin-right: 6.5%;
      float: left;
    }
    .three-col-list__col--right {
      margin-right: 0;
    }

  }

}

.link-list {
  display: inline-block;
  margin-bottom: $gutter-half;
}

.link-list__item {
  display: block;
  margin-bottom: $gutter-half;
}
